import md5 from 'blueimp-md5'

function getNowTime() {
	return parseInt(new Date().getTime())
}

function getSign(time) {
	return md5('fungoa8sport' + time)
}

function isIOS() {
	var ua = navigator.userAgent || navigator.vendor || window.opera
	return /ip(hone|od|ad)/i.test(ua)
}

function isAndrod() {
//	var ua = (navigator.userAgent || navigator.vendor || window.opera);
//	return /android/i.test(ua);
	return checkDevice().versions.android?true:false;
}

function getPara(name) {
	if(name = (new RegExp('[?&]' + encodeURIComponent(name) + '=([^&]*)')).exec(location.href)) {
		return decodeURIComponent(name[1]);
	}
}

function checkDevice() {
  var browser={
    versions:function(){
      var u = navigator.userAgent;
      return {
        trident: u.indexOf('Trident') > -1, //IE内核
        presto: u.indexOf('Presto') > -1, //opera内核
        webKit: u.indexOf('AppleWebKit') > -1, //苹果、谷歌内核
        gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1,//火狐内核
        mobile: !!u.match(/AppleWebKit.*Mobile.*/), //是否为移动终端
        ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios终端
        android: u.indexOf('Android') > -1 || u.indexOf('Adr') > -1 || u.indexOf('Linux') > -1, //android终端
        iPhone: u.indexOf('iPhone') > -1 , //是否为iPhone或者QQHD浏览器
        iPad: u.indexOf('iPad') > -1, //是否iPad
        webApp: u.indexOf('Safari') == -1, //是否web应该程序，没有头部与底部
        qq: u.match(/\sQQ/i) == " qq" //是否QQ
      };
    }(),
    language:(navigator.browserLanguage || navigator.language).toLowerCase()
  }
  return browser
}

//写cookies
function setCookie(name, value) {
	var Days = 30;
	var exp = new Date();
	exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
	document.cookie = name + "=" + escape(value) + ";expires=" + exp.toGMTString();
}

//读取cookies
function getCookie(name) {
	var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");

	if(arr = document.cookie.match(reg))
		return(arr[2]);
	else
		return null;
}

function cpost(v, cdata) {
	let nwtime = getNowTime()
	let sign = getSign(nwtime)
	let param = {
		'time': nwtime,
		'sign': sign
	}
	let HOST = '//api.007kq.com/' + cdata.url
	// let HOST = '//47.75.119.67:8887/' + cdata.url // 测试环境
	// let HOST = '//192.168.1.34:8022/' +cdata.url

	for(let key in cdata.postData) {
		param[key] = cdata.postData[key]
	}

	v.$http.jsonp(HOST, {
			params: param
		})
		.then(response => {
			response = response.body
			cdata.callback(response)
		}, err => {
			console.log('err', err)
			cdata.error()
		})
}
function dateFormate(date, mate) {
  //yyyy-MM-dd hh:mm:ss
  Date.prototype.Format = function (fmt) {
    var o = {
      "M+": this.getMonth() + 1, //月份
      "d+": this.getDate(), //日
      "h+": this.getHours(), //小时
      "m+": this.getMinutes(), //分
      "s+": this.getSeconds(), //秒
      "q+": Math.floor((this.getMonth() + 3) / 3), //季度
      "S": this.getMilliseconds() //毫秒
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
      if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
  }
  var date = new Date(date)
  return date.Format(mate)
}
export {
	getNowTime,
	getSign,
	isIOS,
	isAndrod,
	getPara,
	setCookie,
	getCookie,
	checkDevice,
	cpost,
  dateFormate
}



